import L from 'leaflet';

export async function downloadOfflineMap(bounds, minZoom, maxZoom, onProgress) {
  const bufferInMeters = 500; // 500 –º–µ—Ç—Ä–æ–≤ –±—É—Ñ–µ—Ä –≤–æ–∫—Ä—É–≥ –º–∞—Ä—à—Ä—É—Ç–∞
  const bufferedBounds = bounds.pad(bufferInMeters / bounds.getCenter().distanceTo(bounds.getNorthWest()));

  const tiles = [];
  let totalTiles = 0;
  let loadedTiles = 0;

  for (let z = minZoom; z <= maxZoom; z++) {
    const northWest = bufferedBounds.getNorthWest();
    const southEast = bufferedBounds.getSouthEast();
    const tileBounds = L.bounds(
      L.CRS.EPSG3857.latLngToPoint(northWest, z),
      L.CRS.EPSG3857.latLngToPoint(southEast, z)
    );
    
    for (let x = Math.floor(tileBounds.min.x / 256); x < Math.ceil(tileBounds.max.x / 256); x++) {
      for (let y = Math.floor(tileBounds.min.y / 256); y < Math.ceil(tileBounds.max.y / 256); y++) {
        const url = `https://tile.openstreetmap.org/${z}/${x}/${y}.png`;
        tiles.push(url);
      }
    }
  }

  totalTiles = tiles.length;

  const cache = await caches.open('offline-map-tiles');
  await Promise.all(tiles.map(url => 
    fetch(url)
      .then(response => cache.put(url, response))
      .then(() => {
        loadedTiles++;
        if (onProgress) {
          onProgress(loadedTiles / totalTiles);
        }
      })
  ));
}